import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { TextInput } from '@abyss/web/ui/TextInput';
import { SelectInput } from '@abyss/web/ui/SelectInput';
import { Button } from '@src/common/Button';
import { Layout } from '@abyss/web/ui/Layout';
import { useCurrentUser } from '@src/hooks/user/useCurrentUser';
import { Card } from '@src/abyss/web/ui/Card';
import { Alert } from '@abyss/web/ui/Alert';
import { AccessGroups } from '../../NamespaceSettingsPage/AccessGroups';

export const NamespaceCreateForm = ({ alert, setAlert }) => {
  const user = useCurrentUser();

  const userGroupOptions = useMemo(() => {
    return user.groups.map((groupName) => {
      return {
        label: groupName,
        value: groupName,
      };
    });
  }, [user.groups]);

  return (
    <React.Fragment>
      <Card>
        <Card.Section>
          <TextInput
            label="Product Namespace"
            model="namespace.displayName"
            validators={{ required: true }}
            maxLength={100}
          />
          <SelectInput
            label="Admin Group"
            placeholder="Select group..."
            model="namespace.adminGroup"
            options={userGroupOptions}
            validators={{ required: true }}
            isSearchable
          />
        </Card.Section>
      </Card>
      <AccessGroups />
      <Alert
        title={alert.title}
        isVisible={!!alert.message}
        onClose={() => {
          return setAlert({});
        }}
      >
        {alert.message}
      </Alert>
      <Card>
        <Card.Section>
          <Layout.Group alignLayout="right">
            <Button variant="outline" href="/">
              Cancel
            </Button>
            <Button type="submit">Create Namespace</Button>
          </Layout.Group>
        </Card.Section>
      </Card>
    </React.Fragment>
  );
};

NamespaceCreateForm.propTypes = {
  alert: PropTypes.shape({
    title: PropTypes.string,
    message: PropTypes.string,
  }),
  setAlert: PropTypes.func.isRequired,
};

NamespaceCreateForm.defaultProps = {
  alert: {},
};
