import React, { useEffect, useState } from 'react';
// import axios from 'axios';
// import { config } from '@abyss/web/tools/config';
// import { Alert } from '@abyss/web/ui/Alert';
// import { useGetUser } from '@src/hooks/user/useGetUser';
// import { UserContext } from '@src/hooks/user/useCurrentUser/UserContext';
import { MaelstromContext } from './MaelstromContext';

export const MaelstromProvider = (props) => {
  const [user, setUser] = useState();

  // const [getMongoDBUserResult, getMongoDBUser] = useGetUser({
  //   onCompleted: (response) => {
  //     setUser((prevUser) => {
  //       return {
  //         ...prevUser,
  //         ...response.data,
  //       };
  //     });
  //   },
  // });

  useEffect(() => {
    fetch('/api/identity/abyss-msid/userinfo', {
      credentials: 'include',
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        setUser(data);
      });
  }, []);

  if (!user) {
    return null;
  }

  return <MaelstromContext.Provider {...props} value={user} />;
};
