import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import GetOrganizations from './GetOrganizations.graphql';

export const useGetOrganizations = (options) => {
  return useQuery(GetOrganizations, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'organizations',
    initialState: [],
  });
};
