import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { Router } from '@abyss/web/ui/Router';
import { Heading } from '@abyss/web/ui/Heading';
import { Layout } from '@abyss/web/ui/Layout';
import { Grid } from '@abyss/web/ui/Grid/Grid';
import { useBreadcrumbs } from '@abyss/web/hooks/useBreadcrumbs';
import { Breadcrumbs } from '@abyss/web/ui/Breadcrumbs';

const HeaderStyle = styled('header', {
  padding: '0 $md $md 0',
});

const HeadingStyle = styled(Heading, {
  fontSize: '2.5rem',
});

const DescriptionStyle = styled('p', {
  color: '$gray6',
  maxWidth: 500,
  marginTop: '$sm',
  marginBottom: '$lg',
  fontSize: '$lg',
});

export const PageLayout = ({
  children,
  title,
  description,
  breadcrumbs,
  actions,
}) => {
  const breadcrumbsItems = useBreadcrumbs();

  useEffect(() => {
    if (breadcrumbs) {
      breadcrumbsItems.setBreadcrumbs(breadcrumbs);
    }
  }, [breadcrumbs]);

  return (
    <React.Fragment>
      <Breadcrumbs items={breadcrumbsItems.getBreadcrumbs()} />
      <Layout.Space />
      <Grid
        space={{ xs: 8 }}
        css={{
          'abyss-grid': {
            justifyContent: 'space-between',
            alignItems: 'center',
          },
        }}
      >
        <Grid.Col>
          <If condition={title}>
            <Router.MetaTags title={title} />
            <HeaderStyle>
              <HeadingStyle>{title}</HeadingStyle>
              <If condition={description}>
                <DescriptionStyle>{description}</DescriptionStyle>
              </If>
            </HeaderStyle>
          </If>
        </Grid.Col>
        <If condition={actions}>
          <Grid.Col>
            <div>{actions}</div>
          </Grid.Col>
        </If>
      </Grid>
      {children}
    </React.Fragment>
  );
};

PageLayout.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string,
  description: PropTypes.string,
  breadcrumbs: PropTypes.arrayOf(PropTypes.shape({})),
  actions: PropTypes.node,
};

PageLayout.defaultProps = {
  title: null,
  description: null,
  breadcrumbs: null,
  actions: null,
};
