import { useContext, useState, useMemo } from 'react';
import { jsonKey } from '@abyss/web/tools/jsonKey';
import { storage } from '@abyss/web/tools/storage';
import { event } from '@abyss/web/tools/event';
import { stopwatch } from '@abyss/web/tools/stopwatch';
import merge from 'lodash/merge';
import get from 'lodash/get';
import axios from 'axios';
import { QueryContext } from './QueryContext';

const CACHE = {};
const CACHE_MAX_AGE = 15 * 60 * 1000; // 15 minutes
const DEFAULT_ERROR = `The system is unable to respond at the moment. \
  Please try refreshing the page (click on REFRESH at the top of the page \
  or press F5) or try again at a later time.`;

const memoryStore = {
  getItem: (key) => {
    return CACHE[key];
  },
  setItem: (key, value) => {
    CACHE[key] = value;
  },
  removeItem: (key) => {
    delete CACHE[key];
  },
};

const memoryCache = {
  get: (key) => {
    return storage.getItem(memoryStore, key);
  },
  set: (key, value) => {
    return storage.setItem(memoryStore, key, value, CACHE_MAX_AGE);
  },
  del: (key) => {
    return memoryStore.removeItem(key);
  },
};

const useQueryState = (queryName, initialQueryState, options) => {
  const queryContext = useContext(QueryContext);

  if (queryContext && options.requestPolicy !== 'no-cache') {
    let queryResult = queryContext.queryState[queryName];
    if (!queryResult) {
      queryResult = initialQueryState;
    }

    const setQueryResult = (result) => {
      queryContext.setQueryState((prevState) => {
        return {
          ...prevState,
          [queryName]: result,
        };
      });
    };

    return [queryResult, setQueryResult];
  }

  return useState(initialQueryState);
};

export const useQuery = (query, queryOptions) => {
  const options = queryOptions || {};
  const initialQueryState = {
    loading: false,
    data: options.initialState || null,
    error: null,
  };

  const [queryName, queryType] = useMemo(() => {
    const queryMeta = query.definitions.find((def) => {
      return def.kind === 'OperationDefinition';
    });
    return [queryMeta.name.value, queryMeta.operation];
  }, []);

  if (queryType === 'mutation' && !options.requestPolicy) {
    options.requestPolicy = 'no-cache';
  }

  const [queryResult, setQueryResult] = useQueryState(
    queryName,
    initialQueryState,
    options
  );

  const queryAction = async (params) => {
    const queryParams = params || {};
    if (options.onCalled) {
      options.onCalled();
    }

    const queryBody = query.loc.source.body;
    const queryVars = queryParams.variables;
    const token = queryVars ? `${queryName}:${jsonKey(queryVars)}` : queryName;
    const cachedRequest = memoryCache.get(token);

    setQueryResult({
      ...initialQueryState,
      loading: true,
    });

    if (cachedRequest) {
      return cachedRequest.then(
        (response) => {
          if (options.onCompleted) {
            options.onCompleted(response);
          }

          setQueryResult({
            ...initialQueryState,
            data: response.data,
          });
          return response;
        },
        (error) => {
          if (options.onError) {
            options.onError(error);
          }

          setQueryResult({
            ...initialQueryState,
            error,
          });

          throw error;
        }
      );
    }

    const timer = stopwatch.start();

    const requestPath = `${options.url}?q=${queryName}`;
    const request = axios(requestPath, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json',
      },
      withCredentials: true,
      data: JSON.stringify({
        operationName: queryName,
        query: queryBody,
        variables: queryVars,
      }),
    })
      //   .then(response => {
      //     return response.json().then(json => {
      //       return {
      //         status: response.status,
      //         duration: timer.stop(),
      //         body: json,
      //       };
      //     });
      //   })
      .then((response) => {
        if (response.data && response.data.errors) {
          const serverError = response.data.errors[0];
          const { extensions } = serverError;
          const { exception = {} } = extensions;

          serverError.type = extensions.code;
          serverError.status = response.status;
          serverError.duration = response.duration;

          if (extensions.response) {
            serverError.status = extensions.response.status;
          }

          if (exception.code) {
            serverError.code = exception.code;
          }

          const debug = {
            operationName: queryName,
            type: serverError.type,
            // variables: jsonKey(queryVariables),
            code: serverError.code,
            path: serverError.path,
            stack: exception.stacktrace,
          };

          serverError.debug = JSON.stringify(debug);

          throw serverError;
        }

        const modifiedResponse = { ...response };

        let dataKeyPath = ['data'];
        if (options.dataKey) {
          dataKeyPath = dataKeyPath.concat(options.dataKey);
        }

        modifiedResponse.data = get(response.data, dataKeyPath);

        if (options.initialState) {
          const mergeStruct = Array.isArray(options.initialState) ? [] : {};

          modifiedResponse.data = merge(
            mergeStruct,
            options.initialState,
            modifiedResponse.data
          );
        }

        event('API_TRANSACTION_SUCCESS', {
          ...options.info,
          operationName: queryName,
          status: response.status,
          duration: response.duration,
        });

        if (options.deleteCacheKey) {
          options.deleteCacheKey.forEach((key) => {
            memoryCache.del(key);
          });
        }

        if (options.clearCache) {
          options.clearCache.forEach((item) => {
            let deleteToken = item.key;
            if (item.variables) {
              const deleteJson = {};
              item.variables.forEach((deleteVarKey) => {
                let deleteValue = get(queryVars, deleteVarKey);
                if (!deleteValue && queryVars.input) {
                  deleteValue = get(queryVars, ['input', deleteVarKey]);
                }
                deleteJson[deleteVarKey] = deleteValue;
              });
              deleteToken += `:${jsonKey(deleteJson)}`;
            }

            memoryCache.del(deleteToken);
          });
        }

        if (options.onCache) {
          options.onCache(memoryCache, jsonKey);
        }

        if (options.onCompleted) {
          options.onCompleted(modifiedResponse);
        }

        setQueryResult({
          ...initialQueryState,
          data: modifiedResponse.data,
        });

        return modifiedResponse;
      })
      .catch((err) => {
        memoryCache.del(token);

        const error = err;
        // eslint-disable-next-line no-console
        console.log('useQuery ERROR: ', error);
        error.operationName = queryName;
        if (!error.type) {
          error.type = 'NETWORK_ERROR';
          error.message = DEFAULT_ERROR;
          error.duration = timer.stop();
        }

        event('API_TRANSACTION_ERROR', {
          ...options.info,
          operationName: error.operationName,
          status: error.status,
          duration: error.duration,
          code: error.code,
          message: error.message,
          debug: error.debug,
        });

        if (options.onError) {
          options.onError(error);
        }

        setQueryResult({
          loading: false,
          data: options.initialState || null,
          error,
        });

        throw error;
      });

    if (options.requestPolicy !== 'no-cache') {
      memoryCache.set(token, request);
    }

    return request;
  };

  return [queryResult, queryAction];
};
