import React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { useAbyssProps } from '@abyss/web/hooks/useAbyssProps';

const FooterStyledContainer = styled('div', {
  display: 'flex',
  flexDirection: 'column',
  flexFlow: 'column wrap',
  alignItems: 'flex-start',
});

const FooterStyledTitle = styled('p', {
  fontSize: '1rem',
  fontWeight: '700',
  color: '$footerSectionTitle',
  paddingTop: '1rem',
});

const FooterListContainer = styled('ul', {});

export const PageFooterSection = ({ title, children, ...props }) => {
  const abyssProps = useAbyssProps(props);

  return (
    <FooterStyledContainer {...abyssProps('page-footer-section')}>
      <FooterStyledTitle {...abyssProps('page-footer-section-title')}>
        {title}
      </FooterStyledTitle>
      <FooterListContainer {...abyssProps('page-footer-list-container')}>
        {children}
      </FooterListContainer>
    </FooterStyledContainer>
  );
};

PageFooterSection.displayName = '@abyss/web/ui/PageFooter.Section';

PageFooterSection.propTypes = {
  children: PropTypes.node,
  title: PropTypes.string,
};

PageFooterSection.defaultProps = {
  children: null,
  title: null,
};
