import React from 'react';
import { styled } from '@abyss/web/tools/styled';
import { Layout } from '@abyss/web/ui/Layout';
import { PageFooter } from './PageFooter';
import { useWindowSize } from '@src/hooks/useWindowSize';
import { sections } from './sections';

const StyledLayoutGroup = styled(Layout.Group, {
  display: 'flex',
  width: '100%',
  justifyContent: 'center !important',
});

const getColumns = (numColumns, columnSize) => {
  const columns = [];
  let sectionIndex = 0;
  for (let col = 0; col < numColumns; col++) {
    const rows = [];
    let size = 0;
    while (size < columnSize && sectionIndex < sections.length) {
      const section = sections[sectionIndex];
      if (size + section.size <= columnSize) {
        rows.push(
          <PageFooter.Section key={section.title} title={section.title}>
            {section.links.map((link) => {
              return (
                <PageFooter.Link key={link.url} href={link.url}>
                  {link.text}
                </PageFooter.Link>
              );
            })}
          </PageFooter.Section>
        );
        size += section.size;
        sectionIndex++;
      } else {
        size++;
      }
    }
    columns.push(
      <Layout.Stack key={col} alignItems="left" space={0}>
        {rows}
      </Layout.Stack>
    );
  }
  return columns;
};

export const HCPFooter = () => {
  const { width } = useWindowSize();
  const numColumns = width < 1120 ? 4 : 5;
  const columnSize = width < 1120 ? 5 : 4;

  const subFooterLinks = [
    {
      label: 'Github',
      href: 'https://github.com/uhc-tech/volcan',
      target: '_blank',
    },
  ];

  return (
    <PageFooter
      subFooterLinks={subFooterLinks}
      subFooterTitle="UnitedHealth Group"
    >
      <StyledLayoutGroup grow alignItems="top" space={10}>
        {getColumns(numColumns, columnSize)}
      </StyledLayoutGroup>
    </PageFooter>
  );
};
