import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import CreateRepo from './CreateRepo.graphql';

export const useCreateRepo = (options) => {
  return useQuery(CreateRepo, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'createRepo',
    initialState: {
      data: {},
      name: '',
      fullName: '',
      organization: '',
      url: '',
    },
  });
};
