import React from 'react';
import PropTypes from 'prop-types';
import { FocusScope } from '@react-aria/focus';
import { LoadingOverlayContent } from './LoadingOverlayContent';

export const LoadingOverlay = (props) => {
  return (
    <FocusScope>
      <LoadingOverlayContent {...props} />
    </FocusScope>
  );
};

LoadingOverlay.displayName = '@abyss/web/ui/LoadingOverlay';

LoadingOverlay.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func,
  loadingTitle: PropTypes.string,
  loadingMessage: PropTypes.string,
  statusTitle: PropTypes.string,
  statusMessage: PropTypes.string,
  model: PropTypes.string,
  isOpen: PropTypes.bool,
  isLoading: PropTypes.bool,
  ariaLoadingLabel: PropTypes.string,
  loadingIcon: PropTypes.node,
  statusIcon: PropTypes.oneOf(['success', 'error', 'warning', 'info']),
  width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  isDismissable: PropTypes.bool,
};

LoadingOverlay.defaultProps = {
  children: null,
  onClose: () => {},
  loadingTitle: null,
  loadingMessage: null,
  statusTitle: null,
  statusMessage: null,
  model: null,
  isOpen: false,
  isLoading: false,
  ariaLoadingLabel: 'Loading',
  loadingIcon: null,
  statusIcon: 'info',
  width: '80%',
  isDismissable: false,
};
