import React, { useEffect, useState } from 'react';
import { useForm } from '@abyss/web/hooks/useForm';
import { FormProvider } from '@abyss/web/ui/FormProvider';
import { LoadingOverlay } from '@src/abyss/web/ui/LoadingOverlay';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { useGetNamespaces } from '@src/hooks/namespace/useGetNamespaces';
import { useUpdateNamespace } from '@src/hooks/namespace/useUpdateNamespace';
import { useGetNamespace } from '@src/hooks/namespace/useGetNamespace';
import { PageLayout } from '@src/common/PageLayout';
import { FormContainer } from '@src/common/FormContainer';
import { Grid } from '@abyss/web/ui/Grid';

import { NamespaceSettingsForm } from './NamespaceSettingsForm';
import { NamespaceSettingsDanger } from './NamespaceSettingsDanger';
import { ImplementationDetails } from './ImplementationDetails';

export const NamespaceSettingsPage = () => {
  const router = useRouter();
  const routeParams = router.getRouteParams();
  const form = useForm();

  const [alert, setAlert] = useState({});

  const { namespaceKey } = routeParams;

  const [, getNamespaces] = useGetNamespaces();
  const [getNamespaceResult, getNamespace] = useGetNamespace({
    onCompleted: (result) => {
      if (!result.data.isAdmin) {
        router.navigate('/noaccess');
      } else {
        form.setValue('namespace', result.data);
        const mappedAccessGroups = (result.data.accessGroups || []).map(
          (group) => {
            return { group };
          }
        );
        form.setValue('namespace.accessGroups', mappedAccessGroups);
      }
    },
    onError: (error) => {
      if (error.message === 'User does not have access to the namespace') {
        router.navigate('/noaccess');
      } else {
        setAlert({
          title: 'Error when retrieving namespace',
          message: error.message,
        });
      }
    },
  });

  const [updateNamespaceResult, updateNamespace] = useUpdateNamespace({
    variables: { namespaceKey },
    onCompleted: () => {
      router.navigate(`/${namespaceKey}`);

      getNamespaces();
    },
    onError: (error) => {
      setAlert({
        title: 'Error when updating namespace',
        message: error.message,
      });
    },
  });

  useEffect(() => {
    if (namespaceKey) {
      getNamespace({ variables: { namespaceKey } });
    }
  }, [namespaceKey]);

  const onSubmitNewNamespace = (data) => {
    const accessGroupsArray = (data.namespace.accessGroups || []).map(
      (item) => {
        return item.group;
      }
    );
    updateNamespace({
      variables: {
        namespaceKey,
        namespaceId: getNamespaceResult.data.namespaceId,
        displayName: data.namespace.displayName,
        adminGroup: data.namespace.adminGroup,
        accessGroups: accessGroupsArray,
      },
    });
  };

  return (
    <PageLayout
      title="Namespace Settings"
      breadcrumbs={[
        {
          id: 'namespace-list-page',
          title: 'Home',
          href: '/',
        },
        {
          id: 'table-list-page',
          title: getNamespaceResult.data.displayName,
          href: `/${namespaceKey}`,
        },
        {
          id: 'namespace-settings-page',
          title: 'Namespace Settings',
          href: `/${namespaceKey}/settings`,
        },
      ]}
    >
      <LoadingOverlay
        loadingTitle="Please Wait"
        loadingMessage="preparing namespace settings..."
        isLoading={getNamespaceResult.loading}
      >
        <LoadingOverlay
          loadingTitle="Please Wait"
          loadingMessage="while your namespace is updated."
          isLoading={updateNamespaceResult.loading}
        >
          <Grid>
            <Grid.Col>
              <FormContainer>
                <FormProvider state={form} onSubmit={onSubmitNewNamespace}>
                  <NamespaceSettingsForm
                    namespaceKey={namespaceKey}
                    isSuperAdmin={getNamespaceResult.data.isSuperAdmin}
                    alert={alert}
                    setAlert={setAlert}
                  />
                </FormProvider>
                <NamespaceSettingsDanger namespaceKey={namespaceKey} />
              </FormContainer>
            </Grid.Col>
            <Grid.Col>
              <ImplementationDetails
                namespaceKey={namespaceKey}
                ldKey={getNamespaceResult.data.ldKey}
              />
            </Grid.Col>
          </Grid>
        </LoadingOverlay>
      </LoadingOverlay>
    </PageLayout>
  );
};
