import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import GetMetadataFromVitals from './GetMetadataFromVitals.graphql';

export const useGetMetadataFromVitals = (options) => {
  return useQuery(GetMetadataFromVitals, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'metadataFromVitals',
    initialState: {
      aideId: '',
    },
  });
};
