import React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { CodeHighlighter } from '@abyss/web/ui/CodeHighlighter';
import { FormContainer } from '@src/common/FormContainer';
import { Card } from '@src/abyss/web/ui/Card';

const HeadingText = styled('div', {
  color: '$primary1',
  fontWeight: '$bold',
  fontSize: '16px',
});

const LAGOON_COMPONENT_CODE = `import { useLagoon } from '@abyss/web/hooks/useLagoon';

export const ExampleComponent = () => {
  const featureFlags = useLagoon('feature-flags');
  const errorMessages = useLagoon('error-messages.message');

  return (
    <React.Fragment>
      <h1>Example</h1>
      <If condition={featureFlags('TEXT_TOGGLE')}>
        <div>Text Toggle is On</div>
      </If>
      <div>
        Error Message - {errorMessages('MESSAGE_ID_1')}
      </div>
    </React.Fragment>
  );
};`;

const getProviderCode = (namespaceKey) => {
  return `import { LagoonProvider } from '@abyss/web/ui/LagoonProvider';

<LagoonProvider app="${namespaceKey}" env="stage">
  <App />
</LagoonProvider>`;
};

const getS3Url = (namespaceKey) => {
  return `https://maelstrom-dmz.uhcprovider.com/app/lagoon/${namespaceKey}/env/stage`;
};

const getLaunchDarklyUrl = (ldKey) => {
  return `https://app.launchdarkly.com/api/v2/flags/${ldKey}?env=stage`;
};

export const ImplementationDetails = ({ namespaceKey, ldKey }) => {
  const lagoonProviderCode = getProviderCode(namespaceKey);
  return (
    <FormContainer>
      <Card>
        <Card.Section>
          <HeadingText>Lagoon Provider Example</HeadingText>
          <CodeHighlighter code={lagoonProviderCode} language="js" />
        </Card.Section>
        <Card.Section>
          <HeadingText>Component Usage Example</HeadingText>
          <CodeHighlighter code={LAGOON_COMPONENT_CODE} language="js" />
        </Card.Section>
        <Card.Section>
          <HeadingText>Amazon S3 URL</HeadingText>
          <div>{getS3Url(namespaceKey)}</div>
        </Card.Section>
        <Card.Section>
          <HeadingText>Launch Darkly URL</HeadingText>
          <div>{getLaunchDarklyUrl(ldKey)}</div>
        </Card.Section>
      </Card>
    </FormContainer>
  );
};

ImplementationDetails.propTypes = {
  namespaceKey: PropTypes.string.isRequired,
  ldKey: PropTypes.string,
};

ImplementationDetails.defaultProps = {
  ldKey: '',
};
