import React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { useAbyssProps } from '@abyss/web/hooks/useAbyssProps';
import { useLink } from '@abyss/web/hooks/useLink';
import { Layout } from '@abyss/web/ui/Layout';

const StyledLink = styled('a', {
  display: 'flex',
  justifyContent: 'start',
  color: '$primary1',
  fontFamily: '$primary',
  fontSize: '$sm',
  lineHeight: 1,
  padding: '$sm $md',
  variant: 'custom',
  width: '100%',
  // borderLeft: '1px solid $gray4',

  '&:hover': {
    backgroundColor: '$gray3',
    textDecoration: 'none',
    color: '$primary1',
  },

  variants: {
    isActive: {
      true: {
        backgroundColor: '$interactive3',
        borderLeft: '1px solid $primary1',
        // fontWeight: '$bold',

        '&:hover': {
          color: '$foreground',
          backgroundColor: '$interactive3',
        },
      },
    },
    variant: {
      sideBarMenu: {},

      defaultVariants: {
        variant: 'default',
      },
    },
    position: {
      left: {},
      right: {},
    },
    isDisabled: {
      true: {
        cursor: 'not-allowed',
        color: '$gray5',
        svg: {
          fill: '$gray5',
        },
        '&:hover': {
          cursor: 'not-allowed',
          textDecoration: 'none',
          color: '$gray5',
          backgroundColor: 'transparent',
        },
      },
    },
    extraPadding: {
      true: {
        paddingLeft: '$lg',
      },
    },
  },
  compoundVariants: [
    {
      variant: 'sideBarMenu',
      position: 'left',
      css: {
        borderLeft: '1px solid $primary1',
      },
    },
    {
      variant: 'sideBarMenu',
      position: 'right',
      css: {
        borderRight: '1px solid $primary1',
        justifyContent: 'end',
      },
    },
  ],
});

const isMatchedPath = (path1, path2) => {
  let pathUpdated = path2 || null;

  if (!pathUpdated && typeof window !== 'undefined') {
    pathUpdated = window.location.pathname;
  }

  return pathUpdated.startsWith(path1);
};

export const SubNavLink = ({
  depth,
  href,
  isDisabled,
  openNewWindow,
  before,
  after,
  title,
  onClick,
  ...props
}) => {
  const abyssProps = useAbyssProps(props);

  const { linkProps } = useLink({
    ...props,
    onClick,
    href,
    isDisabled,
    openNewWindow,
  });

  const isActive = isMatchedPath(href);

  const anchorProps = {
    ...linkProps,
    // position,
    // variant,
    isDisabled,
  };

  const layoutProps = {
    before,
    after,
    space: '$sm',
    css: {
      'abyss-insert-element-root': {
        justifyContent: 'start',
      },
    },
  };

  return (
    <StyledLink
      {...anchorProps}
      {...abyssProps('sub-navigation-menu-link')}
      isActive={isActive}
      extraPadding={depth <= 1}
    >
      <Layout.Insert {...layoutProps}>{title}</Layout.Insert>
    </StyledLink>
  );
};

SubNavLink.propTypes = {
  depth: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  href: PropTypes.string.isRequired,
  isDisabled: PropTypes.bool,
  openNewWindow: PropTypes.bool,
  before: PropTypes.node,
  after: PropTypes.node,
  onClick: PropTypes.func,
};

SubNavLink.defaultProps = {
  isDisabled: false,
  openNewWindow: false,
  before: null,
  after: null,
  onClick: null,
};
