import React from 'react';
import { styled } from '@abyss/web/tools/styled';
import { Button as AbyssButton } from '@abyss/web/ui/Button';

const StyledButton = styled(AbyssButton, {
  '&.abyss-button-root': {
    borderRadius: '0.375rem',
  },
});

const OutlinedButton = styled(StyledButton, {
  '&.abyss-button-root': {
    '&:hover': {
      backgroundColor: '#EDF2F7',
    },
  },
});

const SolidButton = styled(StyledButton, {
  '&.abyss-button-root': {
    '&:hover': {
      backgroundColor: '#5A81AF',
      border: 'solid 2px #5A81AF',
    },
  },
});

export const Button = (props) => {
  const { variant, isDisabled } = { ...props };
  if (isDisabled) {
    return <StyledButton {...props} variant={variant} isDisabled />;
  }
  return variant === 'outline' ? (
    <OutlinedButton {...props} variant={variant} />
  ) : (
    <SolidButton {...props} variant={variant} />
  );
};
