import { config } from '@abyss/web/tools/config';
import { useQuery } from '@src/abyss/web/hooks/useQuery';
import CreateNamespace from './CreateNamespace.graphql';

export const useCreateNamespace = (options) => {
  return useQuery(CreateNamespace, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'createNamespace',
    clearCache: [{ key: 'GetNamespaces' }],
  });
};
