import React from 'react';
import PropTypes from 'prop-types';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';
import { Layout } from '@abyss/web/ui/Layout';
import { styled } from '@abyss/web/tools/styled';
import { useAbyssProps } from '@abyss/web/hooks/useAbyssProps';
import { useAbyssTheme } from '@abyss/web/hooks/useAbyssTheme';
import { Link } from '@abyss/web/ui/Link';

const LinkIcon = styled(IconMaterial, {
  transition: 'transform 0.3s ease',
});

const LinkItem = styled('li', {
  marginBottom: '5px',
  '&:hover': {
    textDecoration: 'none',
    cursor: 'pointer',
    [LinkIcon]: {
      transform: 'translate3d(5px, 0, 0)',
    },
  },
});

const StyledLink = styled(Link, {
  color: '$footerSectionLink',
  fontSize: '0.875rem !important',
  fontWeight: '400',
  lineHeight: '0.875rem !important',
  textUnderlineOffset: '2px',
  textDecoration: 'none',
  wordWrap: 'break-word',
  '&:hover': {
    color: '$footerSectionLinkHover',
    textDecoration: 'underline',
  },
});

export const PageFooterLink = ({ children, href, onClick, ...props }) => {
  const abyssProps = useAbyssProps(props);
  const abyssTheme = useAbyssTheme();

  const renderIcon = abyssTheme?.themeName !== 'optum';

  return (
    <LinkItem {...abyssProps('page-footer-link-item')}>
      <StyledLink
        {...abyssProps('page-footer-link')}
        href={href}
        onClick={onClick}
        hideIcon
        variant="custom"
      >
        <Layout.Insert
          {...abyssProps('page-footer-link-icon-insert')}
          after={
            renderIcon && (
              <LinkIcon
                color="$footerText"
                icon="chevron_right"
                {...abyssProps('page-footer-link-icon')}
              />
            )
          }
        >
          {children}
        </Layout.Insert>
      </StyledLink>
    </LinkItem>
  );
};

PageFooterLink.displayName = '@abyss/web/ui/PageFooter.Link';

PageFooterLink.propTypes = {
  children: PropTypes.node,
  href: PropTypes.string,
  onClick: PropTypes.func,
};

PageFooterLink.defaultProps = {
  children: null,
  href: null,
  onClick: null,
};
