import React from 'react';
import PropTypes from 'prop-types';
import { Card } from '@src/abyss/web/ui/Card';
import { styled } from '@abyss/web/tools/styled';
import { Link } from '@abyss/web/ui/Link';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';

const StyledGrid = styled('div', {
  alignContent: 'center',
  alignItems: 'center',
  display: 'flex',
});

const LinkTitle = styled('div', {
  overflow: 'hidden',
  textOverflow: 'ellipsis',
  whiteSpace: 'nowrap',
  width: '100%',
  textAlign: 'center',
});

const StyledLink = styled(Link, {
  width: '100%',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  border: '3px solid $gray4',
  borderRadius: 5,
  height: 180,
  margin: 10,
  padding: 10,
  transition: 'all 200ms ease',
  transitionPropery: 'background, borderColor',

  [LinkTitle]: {
    color: '$primary1',
  },

  svg: {
    fill: '$primary1',
  },

  '&:hover': {
    background: '$primary1',
    borderColor: '$primary1',

    [LinkTitle]: {
      color: '$white',
    },

    svg: {
      fill: '$white',
    },
  },
});
export const SelectionView = ({ options }) => {
  return (
    <Card>
      <StyledGrid>
        {options.map((option) => {
          return (
            <StyledLink key={option.label} href={option.href} variant="custom">
              <IconMaterial icon="layers" size={40} />
              <LinkTitle>{option.label}</LinkTitle>
            </StyledLink>
          );
        })}
      </StyledGrid>
    </Card>
  );
};

SelectionView.propTypes = {
  options: PropTypes.arrayOf(PropTypes.shape({})),
};

SelectionView.defaultProps = {
  options: [],
};
