import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import CreateMigrationTask from './CreateMigrationTask.graphql';

export const useCreateMigrationTask = (options) => {
  return useQuery(CreateMigrationTask, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'createMigrationTask',
    initialState: {},
  });
};
