import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useFocusManager } from '@react-aria/focus';
import { styled } from '@abyss/web/tools/styled';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';
import { Alert } from '@abyss/web/ui/Alert';
import { LoadingSpinner } from '@abyss/web/ui/LoadingSpinner';
import { useAbyssProps } from '@abyss/web/hooks/useAbyssProps';
import { Focusable } from '@abyss/web/ui/Focusable';
import { useAbyssId } from '@abyss/web/hooks/useAbyssId';
import { useLoadingOverlay } from './useLoadingOverlay';

const LoadingContainer = styled('div', {
  height: '100%',
  width: '100%',
  position: 'relative',
});

const CloseButton = styled(Focusable, {
  position: 'absolute',
  top: 5,
  right: 5,
  display: 'flex',
  alignSelf: 'flex-start',
  borderRadius: '50%',
  padding: '4px',
  transition: 'all  0.1s',
  '&:hover': {
    backgroundColor: '$gray2',
  },
  '&:active': {
    transform: 'scale(0.9)',
  },
});

const StatusIcon = styled('div', {
  width: 80,
  height: 80,
  borderRadius: '50%',
  flexShrink: 0,

  variants: {
    statusIcon: {
      info: { backgroundColor: '$info2' },
      warning: { backgroundColor: '$warning2' },
      error: { backgroundColor: '$error2' },
      success: { backgroundColor: '$success2' },
    },
  },
});

const Overlay = styled('div', {
  zIndex: 100,
  position: 'absolute',
  top: -1,
  right: -1,
  bottom: -1,
  left: -1,
  opacity: 0.75,
  backgroundColor: '$white',
});

const LoadingDialog = styled('div', {
  static: {
    zIndex: 100,
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    background: 'rgba(255, 255, 255, 1)',
    minHeight: 90,
    border: `1px solid`,
    borderColor: '$gray1',
    boxShadow: `0 0 5px 3px #CCC`,
    borderRadius: 3,
    '&:focus-visible': {
      color: '$interactive1',
      boxShadow: '0px 0px 3px 2px',
    },
  },
  dynamic: ({ cssProps }) => {
    return { width: cssProps.width };
  },
});

const BodyContainer = styled('div', {
  display: 'flex',
  flexDirection: 'row',
  justifyContent: 'center',
  alignItems: 'center',
  overflowY: 'auto',
  maxHeight: 'calc(100vh - 185px)',
  padding: '40px $md',
});

const OverlayTitle = styled('h2', {
  textAlign: 'left',
});

const OverlayMessage = styled('p', {
  textAlign: 'left',
  fontSize: 14,
  color: '$gray8',
  margin: 0,
});

const OverlayContent = styled('div', {
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  paddingLeft: '$lg',
  [OverlayTitle]: {
    textAlign: 'left',
    fontFamily: '$primary',
    fontSize: 26,
    fontWeight: 'bold',
    color: '$gray8',
  },
});

export const LoadingOverlayContent = ({
  children,
  ariaLoadingLabel,
  width,
  ...props
}) => {
  const abyssProps = useAbyssProps(props);
  const focusManager = useFocusManager();

  const {
    isOpen,
    isLoading,
    isDismissable,
    loadingTitle,
    loadingMessage,
    loadingIcon,
    statusTitle,
    statusMessage,
    statusIcon,
    onClose,
    dialogProps,
  } = useLoadingOverlay(props);

  useEffect(() => {
    if (isOpen) {
      document.activeElement.blur();
    }
  }, [isOpen]);

  const handleKeyDown = (event) => {
    const key = event.nativeEvent.code;
    if (key === 'Tab') {
      return event.shiftKey
        ? focusManager.focusPrevious()
        : focusManager.focusNext();
    }
    return null;
  };

  const headingId = useAbyssId();

  return (
    <LoadingContainer
      {...abyssProps('loading-overlay-root')}
      onKeyDown={isOpen ? handleKeyDown : null}
      tabIndex={isOpen ? 0 : null}
      {...dialogProps}
    >
      {children}
      {isOpen && (
        <React.Fragment>
          {children && <Overlay {...abyssProps('loading-overlay-overlay')} />}
          <LoadingDialog
            role="dialog"
            aria-labelledby={headingId}
            tabIndex={isOpen && !isDismissable ? 0 : null}
            cssProps={{
              width,
            }}
            {...abyssProps('loading-overlay-dialog')}
          >
            {isLoading ? (
              <BodyContainer {...abyssProps('loading-overlay-loading-body')}>
                <LoadingSpinner
                  isLoading
                  size="$lg"
                  ariaLoadingLabel={ariaLoadingLabel}
                  css={{ 'abyss-loading-spinner-root': { flexShrink: 0 } }}
                >
                  {loadingIcon}
                </LoadingSpinner>
                <OverlayContent
                  {...abyssProps('loading-overlay-loading-content')}
                >
                  <OverlayTitle
                    id={headingId}
                    {...abyssProps('loading-overlay-loading-title')}
                  >
                    {loadingTitle}
                  </OverlayTitle>
                  <OverlayMessage
                    role="status"
                    {...abyssProps('loading-overlay-loading-message')}
                  >
                    {loadingMessage}
                  </OverlayMessage>
                </OverlayContent>
              </BodyContainer>
            ) : (
              <BodyContainer
                {...abyssProps('loading-overlay-status-body')}
                role="status"
              >
                {isDismissable && (
                  <CloseButton
                    as="button"
                    onClick={onClose}
                    aria-label="close"
                    {...abyssProps('loading-overlay-close-button')}
                  >
                    <IconMaterial
                      icon="close"
                      color="$gray5"
                      size={18}
                      {...abyssProps('loading-overlay-close-icon')}
                    />
                  </CloseButton>
                )}
                <StatusIcon statusIcon={statusIcon}>
                  <Alert.Icon
                    variant={statusIcon}
                    size="50%"
                    css={{
                      'abyss-alert-icon': {
                        flexShrink: 0,
                        position: 'relative',
                        left: '25%',
                        top: '25%',
                      },
                    }}
                  />
                </StatusIcon>
                <OverlayContent
                  {...abyssProps('loading-overlay-status-content')}
                >
                  <OverlayTitle {...abyssProps('loading-overlay-status-title')}>
                    {statusTitle}
                  </OverlayTitle>
                  <OverlayMessage
                    {...abyssProps('loading-overlay-status-message')}
                  >
                    {statusMessage}
                  </OverlayMessage>
                </OverlayContent>
              </BodyContainer>
            )}
          </LoadingDialog>
        </React.Fragment>
      )}
    </LoadingContainer>
  );
};

LoadingOverlayContent.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func,
  loadingTitle: PropTypes.string,
  loadingMessage: PropTypes.string,
  statusTitle: PropTypes.string,
  statusMessage: PropTypes.string,
  isOpen: PropTypes.bool,
  isLoading: PropTypes.bool,
  ariaLoadingLabel: PropTypes.string,
  loadingIcon: PropTypes.node,
  statusIcon: PropTypes.oneOf(['success', 'error', 'warning', 'info']),
  width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  isDismissable: PropTypes.bool,
};

LoadingOverlayContent.defaultProps = {
  children: null,
  onClose: () => {},
  loadingTitle: null,
  loadingMessage: null,
  statusTitle: null,
  statusMessage: null,
  isOpen: false,
  isLoading: false,
  ariaLoadingLabel: 'Loading',
  loadingIcon: null,
  statusIcon: 'info',
  width: '80%',
  isDismissable: false,
};
