import React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { useAbyssProps } from '@abyss/web/hooks/useAbyssProps';

const StyledHeaderTitle = styled('h3', {
  static: {
    fontWeight: '$bold',
    fontSize: '$xl',
    color: '$primary1',
    // paddingTop: '$md',

    variants: {
      variant: {
        sideBarMenu: {
          color: '$gray8 !important',
          marginLeft: '-1px !important',
        },
        defaultVariants: {
          variant: 'default',
        },
      },
      position: {
        right: {},
      },
    },
    compoundVariants: [
      {
        variant: 'sideBarMenu',
        position: 'right',
        css: {
          marginRight: '-1px !important',
        },
      },
    ],
  },
});

const HeaderContainer = styled('div', {
  static: {
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
    padding: '0 $lg',
    [StyledHeaderTitle]: {
      fontFamily: '$primary',
    },
    variants: {
      variant: {
        sideBarMenu: {
          paddingLeft: '0px !important',
        },

        defaultVariants: {
          variant: 'default',
        },
      },
      position: {
        right: {
          paddingRight: '0px !important',
        },
      },
    },
    compoundVariants: [
      {
        variant: 'sideBarMenu',
        position: 'right',
        css: {
          alignItems: 'flex-end',
        },
      },
    ],
  },
});

const StyledHeaderText = styled('p', {
  color: '$gray8',
  fontWeight: '$medium',
  fontSize: '$md',
  margin: 0,
  padding: 0,
});

export const SubNavHeader = ({
  position,
  variant,
  title,
  subText,
  headingLevel,
  ...props
}) => {
  const abyssProps = useAbyssProps(props);

  return (
    <HeaderContainer
      position={position}
      variant={variant}
      {...abyssProps('sub-navigation-header-container')}
    >
      {title && (
        <StyledHeaderTitle
          variant={variant}
          as={headingLevel}
          {...abyssProps('sub-navigation-header-title')}
        >
          {title}
        </StyledHeaderTitle>
      )}
      {subText && (
        <StyledHeaderText {...abyssProps('sub-navigation-header-text')}>
          {subText}
        </StyledHeaderText>
      )}
    </HeaderContainer>
  );
};

SubNavHeader.propTypes = {
  headingLevel: PropTypes.oneOf(['h1', 'h2', 'h3', 'h4', 'h5', 'h6'])
    .isRequired,
  title: PropTypes.node.isRequired,
  subText: PropTypes.string,
  position: PropTypes.string.isRequired,
  variant: PropTypes.string.isRequired,
};

SubNavHeader.defaultProps = {
  subText: null,
};
