import { useContext } from 'react';
import { OverlayContext } from '@abyss/web/hooks/useOverlay/OverlayContext';
import { useAriaProps } from '@abyss/web/hooks/useAriaProps';

export const useLoadingOverlay = ({
  model,
  isOpen,
  isLoading,
  isDismissable,
  loadingTitle,
  loadingMessage,
  loadingIcon,
  statusTitle,
  statusMessage,
  statusIcon,
  onClose,
}) => {
  const overlayContext = useContext(OverlayContext);
  const methods = overlayContext?.methods(model) || null;
  const loadingOverlayState = methods?.getState() || null;

  let loadingOverlayProps = {
    isOpen,
    isLoading,
    isDismissable,
    loadingTitle,
    loadingMessage,
    loadingIcon,
    statusTitle,
    statusMessage,
    statusIcon,
  };

  if (model && loadingOverlayState) {
    loadingOverlayProps = {
      ...loadingOverlayProps,
      ...loadingOverlayState.data,
    };

    if (loadingOverlayState.isOpen) {
      loadingOverlayProps.isOpen = true;
    } else {
      loadingOverlayProps.isOpen = false;
      loadingOverlayProps.isLoading = false;
    }
  }

  const { ariaLabelProps, ariaInputProps } = useAriaProps({});

  return {
    ...loadingOverlayProps,
    isOpen: loadingOverlayProps.isOpen || loadingOverlayProps.isLoading,
    onClose: model && methods ? methods.close : onClose,
    dialogProps: {
      ...ariaInputProps,
      ...(loadingTitle // TODO what is this even doing???
        ? { 'aria-labelledby': ariaLabelProps.id }
        : { 'aria-label': 'loading-overlay-opened' }),
    },
  };
};
