import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import GetResourceGroups from './GetResourceGroups.graphql';

export const useGetResourceGroups = (options) => {
  return useQuery(GetResourceGroups, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'resourceGroups',
    initialState: [],
  });
};
