import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import CreateMigrationTasks from './CreateMigrationTasks.graphql';

export const useCreateMigrationTasks = (options) => {
  return useQuery(CreateMigrationTasks, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'createMigrationTasks',
    initialState: {},
  });
};
