import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { Modal } from '@abyss/web/ui/Modal';
import { Button } from '@src/common/Button';
import { Layout } from '@abyss/web/ui/Layout';
import { TextInput } from '@abyss/web/ui/TextInput';
import { Alert } from '@abyss/web/ui/Alert';
import { useDeleteNamespace } from '@src/hooks/namespace/useDeleteNamespace';

export const DeleteNamespaceModal = ({
  namespaceKey,
  isDeleteOpen,
  setIsDeleteOpen,
}) => {
  const router = useRouter();
  const [alert, setAlert] = useState();
  const [inputValue, setInputValue] = useState();

  useEffect(() => {
    if (isDeleteOpen) {
      setInputValue();
      setAlert();
    }
  }, [isDeleteOpen]);

  const handleClose = () => {
    setIsDeleteOpen(false);
  };

  const [deleteNamespaceResult, deleteNamespace] = useDeleteNamespace({
    onCompleted: () => {
      setIsDeleteOpen(false);
      router.navigate('/');
    },
    onError: (error) => {
      setAlert({
        title: 'Error when deleting the namespace',
        message: error.message,
      });
    },
  });

  const handleConfirmDelete = () => {
    setAlert();
    deleteNamespace({
      variables: {
        namespaceKey,
      },
    });
  };

  const handleChange = (e) => {
    setInputValue(e.target.value);
  };

  return (
    <Modal
      title="Delete Namespace"
      isOpen={isDeleteOpen}
      onClose={handleClose}
      size="500px"
    >
      <Modal.Section>
        This action cannot be undone. This will permanently delete the{' '}
        <b>{namespaceKey}</b> namespace.
      </Modal.Section>
      <Modal.Section>
        Please type <b>{namespaceKey}</b> to confirm.
        <TextInput value={inputValue} onChange={handleChange} />
        <Layout.Space />
        <Button
          isDisabled={inputValue !== namespaceKey}
          variant="outline"
          onClick={handleConfirmDelete}
          css={{
            'abyss-button-root': {
              width: '100%',
            },
          }}
          ariaLoadingLabel="Deleting Namespace"
          isLoading={deleteNamespaceResult.loading}
        >
          I understand, delete this namespace
        </Button>
        {alert && (
          <Modal.Section>
            <Alert title={alert.title}>{alert.message}</Alert>
          </Modal.Section>
        )}
      </Modal.Section>
    </Modal>
  );
};

DeleteNamespaceModal.propTypes = {
  namespaceKey: PropTypes.string.isRequired,
  isDeleteOpen: PropTypes.bool.isRequired,
  setIsDeleteOpen: PropTypes.func.isRequired,
};
