import React, { useEffect, useState, useContext } from 'react';
import kebabCase from 'lodash/kebabCase';
import { Button } from '@src/common/Button';
import { TextInput } from '@abyss/web/ui/TextInput';
import { SelectInput } from '@abyss/web/ui/SelectInput';
import { useGetOrganizations } from '@src/hooks/github/useGetOrganizations';
import { useCreateRepo } from '@src/hooks/github/useCreateRepo';
import { useGetResourceGroups } from '@src/hooks/prm/useGetResourceGroups';
import { useForm } from '@abyss/web/hooks/useForm';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { FormProvider } from '@abyss/web/ui/FormProvider';
import { LoadingOverlay } from '@abyss/web/ui/LoadingOverlay';
import { Layout } from '@abyss/web/ui/Layout';
import { MaelstromContext } from '@src/abyss/utility/ui/MaelstromProvider/MaelstromContext';

export const CreateRepo = () => {
  const form = useForm();
  const user = useContext(MaelstromContext);
  const router = useRouter();

  const [aideId, setAideId] = useState(null);
  const [organizationOptions, setOrganizationOptions] = useState([]);
  const [resourceGroupOptions, setResourceGroupOptions] = useState([]);
  const [orgsResult, getOrgs] = useGetOrganizations({
    onCompleted: (response) => {
      const options = response.data.map((dataItem) => {
        return {
          value: dataItem.name,
          label: dataItem.name,
        };
      });

      setOrganizationOptions(options);
    },
  });
  const [createRepoResult, createRepo] = useCreateRepo({
    onCompleted: (response) => {
      window.open(response.data.url, '_blank', 'noreferrer');
      router.navigate('/');
    },
  });
  const [resourceGroupsResult, getResourceGroups] = useGetResourceGroups({
    onCompleted: (response) => {
      const options = response.data.map((dataItem) => {
        return {
          value: dataItem.alias,
          label: `${dataItem.alias} (${dataItem.application})`,
        };
      });

      setResourceGroupOptions(options);
    },
  });

  useEffect(() => {
    getOrgs();
    getResourceGroups({
      variables: {
        msid: user.username,
      },
    });
  }, []);

  const handleChangeResourceGroup = (value) => {
    const matchedGroup = resourceGroupsResult.data.find((dataItem) => {
      return dataItem.alias === value;
    });

    if (matchedGroup) {
      setAideId(matchedGroup.askId);
      form.setValue('aide', matchedGroup.askId);
      form.setValue('resourceGroupName', matchedGroup.name);
    }
  };

  const handleBlurRepoName = (e) => {
    const name = e.target.value;
    const newRepoName = kebabCase(name);
    form.setValue('repositoryName', newRepoName);
  };

  const handleSubmit = (formData) => {
    const variables = {
      repo: formData.repositoryName,
      org: formData.org,
      aideId: formData.aide,
      resourceGroupName: formData.resourceGroupName,
      resourceGroupAlias: formData.resourceGroup,
    };

    createRepo({ variables });
  };

  return (
    <LoadingOverlay
      isLoading={createRepoResult.isLoading}
      loadingTitle="Loading..."
      loadingMessage="Creating Repository..."
    >
      <FormProvider state={form} onSubmit={handleSubmit}>
        <TextInput
          label="Repository Name"
          validators={{ required: true }}
          model="repositoryName"
          onBlur={handleBlurRepoName}
        />
        <SelectInput
          label="Organization"
          model="org"
          placeholder="Select a target Organization"
          validators={{ required: true }}
          isSearchable
          options={organizationOptions}
          isLoading={orgsResult.isLoading}
        />
        <SelectInput
          label="Resource Group"
          model="resourceGroup"
          validators={{ required: true }}
          placeholder="Select a Resource Group"
          isSearchable
          options={resourceGroupOptions}
          onChange={handleChangeResourceGroup}
          isLoading={resourceGroupsResult.isLoading}
        />
        <TextInput
          label="AIDE ID"
          model="aide"
          validators={{ required: true }}
          isDisabled={aideId}
        />
        <Layout.Space space={24} />
        <Button type="submit">Create Repository</Button>
      </FormProvider>
    </LoadingOverlay>
  );
};
