import React, { useState } from 'react';
import { styled } from '@abyss/web/tools/styled';
import { Layout } from '@abyss/web/ui/Layout';
import { Button } from '@src/common/Button';
import { TextInput } from '@abyss/web/ui/TextInput';
import { useForm } from '@abyss/web/hooks/useForm';
import { FormProvider } from '@abyss/web/ui/FormProvider';
import { LoadingOverlay } from '@abyss/web/ui/LoadingOverlay';
// import { MaelstromContext } from '@src/abyss/utility/ui/MaelstromProvider/MaelstromContext';
import { useGetReposForMigration } from '@src/hooks/github/useGetReposForMigration';
import { useCreateMigrationTasks } from '@src/hooks/github/useCreateMigrationTasks';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { RepoListForm } from './RepoListForm';
import { MigrationModal } from '../Modals';

const GIT_REGEX = /^https?:\/\/(github(?:.optum)?|gitlab).com\/[\w-]+$/;

const FlexContainer = styled('div', {
  display: 'flex',
});

const FlexInput = styled('div', {
  flexGrow: 1,
  marginRight: 8,
});

export const MigrateOrg = () => {
  const orgForm = useForm();

  const router = useRouter();
  const repoListForm = useForm();
  const repoList = repoListForm.createFields({ name: 'repos' });

  const [isSubmitted, setIsSubmitted] = useState(false);

  const [reposResult, getRepos] = useGetReposForMigration({
    onCompleted: (response) => {
      const mappedData = response.data.map((item) => {
        return { ...item, checked: true };
      });
      repoList.replace(mappedData);
    },
  });

  const [migrateTasksResult, createMigrationTasks] = useCreateMigrationTasks({
    onCompleted: (response) => {
      setIsSubmitted(true);
    },
  });

  const handleSubmit = (data) => {
    getRepos({
      variables: {
        url: data.sourceOrgUrl,
      },
    });

    const [org] = data.sourceOrgUrl.split('/').reverse();
    repoListForm.setValue('prefix', org);
  };

  const handleMigrateSubmit = (data) => {
    const mappedRepos = [];

    data.repos.forEach((repo) => {
      if (repo.checked) {
        mappedRepos.push(repo.repo);
      }
    });

    createMigrationTasks({
      variables: {
        org: data.org,
        prefix: data.prefix,
        repos: mappedRepos,
        url: orgForm.getValues(['sourceOrgUrl'])[0],
        archive: !!data.archive,
      },
    });
  };

  const handleClick = () => {
    window.open(
      'https://github.com/uhc-tech/volcan-taskrunner/issues',
      '_blank',
      'noreferrer'
    );
    router.navigate('/');
  };

  const handleClose = () => {
    setIsIssueOpen(false);
  };

  return (
    <LoadingOverlay
      isLoading={false}
      loadingTitle="Loading..."
      loadingMessage="Migrating Repositories..."
    >
      <FormProvider state={orgForm} onSubmit={handleSubmit}>
        <FlexContainer>
          <FlexInput>
            <TextInput
              label="Source Organization URL"
              model="sourceOrgUrl"
              validators={{
                required: true,
                pattern: {
                  value: GIT_REGEX,
                  message: 'Invalid Organization URL',
                },
              }}
              placeholder="https://github.optum.com/org"
            />
          </FlexInput>
          <div>
            <Layout.Space space={26} />
            <Button
              type="submit"
              isLoading={reposResult.isLoading}
              ariaLoadingLabel="Loading Repositories"
            >
              List Repositories
            </Button>
          </div>
        </FlexContainer>
      </FormProvider>

      <If condition={repoList.fields.length > 0}>
        <RepoListForm
          form={repoListForm}
          fields={repoList.fields}
          onSubmit={handleMigrateSubmit}
        />
      </If>
      <MigrationModal
        isOpen={isSubmitted}
        onClose={handleClose}
        onSubmit={handleClick}
      />
    </LoadingOverlay>
  );
};
