import React, { useState } from 'react';
import { useForm } from '@abyss/web/hooks/useForm';
import { FormProvider } from '@abyss/web/ui/FormProvider';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { LoadingOverlay } from '@src/abyss/web/ui/LoadingOverlay';
import { useGetNamespaces } from '@src/hooks/namespace/useGetNamespaces';
import { useCreateNamespace } from '@src/hooks/namespace/useCreateNamespace';
import { PageLayout } from '@src/common/PageLayout';
import { FormContainer } from '@src/common/FormContainer';

import { NamespaceCreateForm } from './NamespaceCreateForm';

export const NamespaceCreatePage = () => {
  const router = useRouter();
  const form = useForm();

  const [alert, setAlert] = useState({});

  const [, getNamespaces] = useGetNamespaces();

  const [createNamespaceResult, createNamespace] = useCreateNamespace({
    onCompleted: (response) => {
      getNamespaces();
      router.navigate(`/${response.data.namespaceKey}`);
    },
    onError: (error) => {
      setAlert({
        title: 'Error when creating namespace',
        message: error.message,
      });
    },
  });

  const onSubmitNewNamespace = (data) => {
    const accessGroupsArray = (data.namespace.accessGroups || []).map(
      (item) => {
        return item.group;
      }
    );
    createNamespace({
      variables: {
        displayName: data.namespace.displayName,
        adminGroup: data.namespace.adminGroup,
        accessGroups: accessGroupsArray,
      },
    });
  };

  return (
    <PageLayout
      title="Create Namespace"
      breadcrumbs={[
        {
          id: 'namespace-list-page',
          title: 'Home',
          href: '/',
        },
        {
          id: 'namespace-create-page',
          title: 'Create Namespace',
          href: '/namespace/create',
        },
      ]}
    >
      <LoadingOverlay
        loadingTitle="Please Wait"
        loadingMessage="while your namespace is created."
        isLoading={createNamespaceResult.loading}
      >
        <FormContainer>
          <FormProvider state={form} onSubmit={onSubmitNewNamespace}>
            <NamespaceCreateForm alert={alert} setAlert={setAlert} />
          </FormProvider>
        </FormContainer>
      </LoadingOverlay>
    </PageLayout>
  );
};
