import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import GetReposForMigration from './GetReposForMigration.graphql';

export const useGetReposForMigration = (options) => {
  return useQuery(GetReposForMigration, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'reposForMigration',
    initialState: [],
  });
};
