import React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { useAbyssProps } from '@abyss/web/hooks/useAbyssProps';

const FooterRoot = styled('footer', {
  position: 'relative',
});

const FooterContainer = styled('div', {
  margin: '0 auto',
  width: '100%',
  maxWidth: '$sizes$pageSize',
  backgroundColor: '$footerBackground',
  borderTop: '1px solid $footerBorderTop',

  variants: {
    fullWidth: {
      true: {
        maxWidth: '100%',
      },
    },
  },
});

const FooterLinks = styled('div', {
  display: 'flex',
  width: '100%',
  padding: '1rem 5rem',
});

const BrandmarkContainer = styled('div', {
  display: 'flex',
  justifyContent: 'space-evenly',
});

const BrandmarkWrapper = styled('div', {
  marginBottom: '$md',
});

const SubFooterWrapper = styled('div', {
  display: 'flex',
  justifyContent: 'space-between',
  color: '$footerBannerLink',
  flexWrap: 'wrap',
  backgroundColor: '$footerBannerBackground',
  height: 48,
  alignItems: 'center',
  padding: '0px 2rem',
});

const CopyrightContainer = styled('p', {
  color: '$footerBannerCopyright',
  marginBottom: 0,
});

const Copyright = styled('small', {
  fontSize: '$sm',
});

const SubFooterNav = styled('nav', {});

const SubFooterListContainer = styled('ul', {
  display: 'flex',
  alignItems: 'center',
});

const SubFooterList = styled('li', {});

const SubFooterLink = styled('a', {
  color: 'white',
  fontSize: '0.875rem',
  '&:hover': {
    color: 'white',
  },
});

const Divider = styled('span', {
  borderLeft: '1px solid $footerBorder',
  display: 'inline-block',
  height: '9px',
  textAlign: 'center',
  marginLeft: '$sm',
  marginRight: '$sm',
});

export const PageFooter = ({
  subFooterLinks,
  subFooterTitle,
  children,
  brandmark,
  fullWidth,
  bottomFixed,
  ...props
}) => {
  const abyssProps = useAbyssProps(props);
  const subLinksLength = subFooterLinks.length;

  const getCurrentYear = () => {
    return new Date().getFullYear();
  };

  const renderFooterChildren = (
    <FooterContainer
      {...abyssProps('page-footer-container')}
      fullWidth={fullWidth}
    >
      {children && (
        <FooterLinks
          {...abyssProps('page-footer-links')}
          role="navigation"
          aria-label="footer navigation"
        >
          {brandmark && (
            <BrandmarkWrapper {...abyssProps('page-footer-brandmark-wrapper')}>
              <BrandmarkContainer
                {...abyssProps('page-footer-brandmark-container')}
              >
                {brandmark}
              </BrandmarkContainer>
            </BrandmarkWrapper>
          )}
          {children}
        </FooterLinks>
      )}
      <SubFooterWrapper {...abyssProps('page-footer-sub-wrapper')}>
        <CopyrightContainer {...abyssProps('page-footer-copyright-container')}>
          <Copyright {...abyssProps('page-footer-copyright')}>
             {getCurrentYear()} {subFooterTitle}
          </Copyright>
        </CopyrightContainer>
        <SubFooterNav
          aria-label="sub footer navigation"
          {...abyssProps('page-footer-sub-nav')}
        >
          <SubFooterListContainer
            {...abyssProps('page-footer-sub-list-container')}
          >
            {subFooterLinks?.map((link, index) => {
              return (
                <React.Fragment key={link.label}>
                  <SubFooterList {...abyssProps('page-footer-sub-list')}>
                    <SubFooterLink
                      {...abyssProps('page-footer-sub-link')}
                      href={link.href}
                      target={link.target}
                    >
                      {link.label}
                    </SubFooterLink>
                    {subLinksLength !== index + 1 && (
                      <Divider {...abyssProps('page-footer-sub-divider')} />
                    )}
                  </SubFooterList>
                </React.Fragment>
              );
            })}
          </SubFooterListContainer>
        </SubFooterNav>
      </SubFooterWrapper>
    </FooterContainer>
  );

  return (
    <FooterRoot {...abyssProps('page-footer-root')}>
      {renderFooterChildren}
    </FooterRoot>
  );
};

PageFooter.displayName = '@abyss/web/ui/PageFooter';

PageFooter.propTypes = {
  children: PropTypes.node,
  subFooterTitle: PropTypes.string,
  brandmark: PropTypes.node,
  subFooterLinks: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string, href: PropTypes.string })
  ),
  fullWidth: PropTypes.bool,
  bottomFixed: PropTypes.bool,
};

PageFooter.defaultProps = {
  children: null,
  subFooterTitle: null,
  subFooterLinks: [],
  brandmark: null,
  fullWidth: false,
  bottomFixed: false,
};
