import { config } from '@abyss/web/tools/config';
import { useQuery } from '@src/abyss/web/hooks/useQuery';
import GetNamespaces from './GetNamespaces.graphql';

export const useGetNamespaces = (options) => {
  return useQuery(GetNamespaces, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'namespaces',
    initialState: [],
  });
};
