import React, { useEffect, useState, useContext } from 'react';
import PropTypes from 'prop-types';
import { Layout } from '@abyss/web/ui/Layout';
import { Text } from '@abyss/web/ui/Text';
import { Button } from '@abyss/web/ui/Button';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';
import { useRouter } from '@abyss/web/hooks/useRouter';
import { styled } from '@abyss/web/tools/styled';
import { HCPLogo } from '@src/common/HCPLogo';
import { MaelstromContext } from '@src/abyss/utility/ui/MaelstromProvider/MaelstromContext';

const NavBox = styled('div', {
  display: 'flex',
  background: '$hcpGrayBlue',
  width: '100%',
  paddingTop: '12px',
  height: 64,
  padding: '$md',
});

const NavMenu = styled(Layout.Group, {
  width: '100%',
  maxHeight: '50px',
  padding: '8px 20px 0px 17px',
  justifyContent: 'space-between !important',
});

const NavGradient = styled('div', {
  height: '72px',
  maxWidth: '100vw',
  background:
    'linear-gradient(90deg, $hcpGradient1 0%, $hcpGradient2 50%, $hcpGradient3 100%)',
});

const Title = styled(Text, {
  marginLeft: '5px',
  marginTop: '0px',
  marginBottom: '4px',
  '&:hover': {
    cursor: 'pointer',
  },
});

const NavButtonText = styled(Text, {});

const Logo = styled('div', {
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'row',
  flexFlow: 'row wrap',
});

const Icon = styled('div', {
  marginTop: '-1px',
  marginRight: '5px',
  '&:hover': {
    cursor: 'pointer',
  },
});

const Divider = styled('div', {
  backgroundColor: '$white',
  height: '27px',
  width: '1px',
  marginTop: '-8px',
  marginLeft: '6px',
  marginRight: '6px',
});

export const HCPHeader = () => {
  const user = useContext(MaelstromContext);
  const router = useRouter();
  const onLogoClick = () => {
    router.navigate(`/`);
  };
  const onHCPLogoClick = () => {
    window.open('https://hcp.uhg.com/', '_self', 'noopener,noreferrer');
  };

  const onUserIconClick = () => {
    window.open(
      'https://hcp.uhg.com/settings/profile',
      '_self',
      'noopener,noreferrer'
    );
  };

  const [userInitials, setUserInitials] = useState(null);

  useEffect(() => {
    if (user.firstName && user.lastName) {
      setUserInitials(`${user.firstName.charAt(0)}${user.lastName.charAt(0)}`);
    }
  }, [user]);

  return (
    <NavGradient>
      <NavBox>
        <NavMenu alignItems="center">
          <Logo>
            <Icon onClick={onHCPLogoClick}>
              {/* <DracoLogo scale={2.5} /> */}
              <HCPLogo />
            </Icon>
            <Divider />
            <Title
              color="$white"
              fontWeight="400"
              size="16px"
              onClick={onLogoClick}
            >
              Volcan
            </Title>
          </Logo>
          <Layout.Group space={16}>
            <Button
              rounded
              size="40px"
              css={{
                marginTop: '-10px',
                backgroundColor: '$hcpGrayBlue2',
                border: 'solid $hcpGrayBlue2',
              }}
              onClick={onUserIconClick}
            >
              {userInitials ? (
                <NavButtonText color="$white" fontWeight="700" size="16px">
                  {userInitials.toUpperCase()}
                </NavButtonText>
              ) : (
                <IconMaterial icon="person" color="$white" />
              )}
            </Button>
          </Layout.Group>
        </NavMenu>
      </NavBox>
    </NavGradient>
  );
};
