import React from 'react';
import { Router } from '@abyss/web/ui/Router';
import { MaelstromProvider } from '@src/abyss/utility/ui/MaelstromProvider';
// import { UserProvider } from '@src/common/UserProvider';
import { AppBody } from '@src/common/AppBody';
import { PageNotFound } from '@src/common/PageNotFound';

// import { NamespaceListPage } from './NamespaceListPage';
import { NamespaceSettingsPage } from './NamespaceSettingsPage';
import { NamespaceCreatePage } from './NamespaceCreatePage';

import { OrganizationMigratePage } from './OrganizationMigratePage';

import { RepositoryCreatePage } from './RepositoryCreatePage';
import { RepositoryMigratePage } from './RepositoryMigratePage';
import { RepositorySelectPage } from './RepositorySelectPage';

export const Routes = () => {
  return (
    <MaelstromProvider>
      <AppBody>
        <Router.Routes title="Healthcare Platform | Volcan - {{title}}">
          <Router.Route
            path="/namespace/create"
            element={<NamespaceCreatePage />}
          />
          <Router.Route
            path="/:namespaceKey/settings"
            element={<NamespaceSettingsPage />}
          />
          <Router.Route
            path="/organization/migrate"
            element={<OrganizationMigratePage />}
          />
          <Router.Route
            path="/repository/create"
            element={<RepositoryCreatePage />}
          />
          <Router.Route
            path="/repository/migrate"
            element={<RepositoryMigratePage />}
          />
          <Router.Route path="/" element={<RepositorySelectPage />} />
          <Router.Route path="*" element={<PageNotFound />} />
        </Router.Routes>
      </AppBody>
    </MaelstromProvider>
  );
};
