import React, { useState } from 'react';
import { styled } from '@abyss/web/tools/styled';
import { useForm } from '@abyss/web/hooks/useForm';
import { useFormFieldArray } from '@abyss/web/hooks/useFormFieldArray';
import { TextInput } from '@abyss/web/ui/TextInput';
import { Card } from '@src/abyss/web/ui/Card';
import { DangerButton } from '@src/common/DangerButton';
import { IconMaterial } from '@abyss/web/ui/IconMaterial';
import { Button } from '@src/common/Button';
// import { AccessGroupRemoveModal } from '@src/common/AccessGroupRemoveModal';

const StyledRow = styled('div', {
  marginBottom: '$sm',
  paddingBottom: '$sm',
});

const Flex = styled('div', {
  display: 'flex',
  alignItems: 'flex-end',
});

const FullWidth = styled('div', {
  width: '100%',
  paddingRight: '$lg',
});

const ButtonWrapper = styled('div', {
  paddingRight: '$md',
});

export const AccessGroups = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [removeAccessGroupName, setRemoveAccessGroupName] = useState('');
  const [removeIndex, setRemoveIndex] = useState();
  const form = useForm();
  const { fields, append, remove } = useFormFieldArray({
    control: form.control,
    name: 'namespace.accessGroups',
  });
  const watchAccessGroup = form.watch('namespace.accessGroup');

  const handleAddAccessGroup = () => {
    if (watchAccessGroup) {
      append({ group: watchAccessGroup });
      form.setValue('namespace.accessGroup', '');
    }
  };

  const handleAccessKeyDown = (e) => {
    if (e.key === 'Enter') {
      handleAddAccessGroup();
      e.preventDefault();
    }
  };

  const handleRemove = (index) => {
    setRemoveIndex(index);
    setRemoveAccessGroupName(fields[index].group);
    setIsOpen(true);
  };

  return (
    <React.Fragment>
      <Card>
        <Card.Section>
          <Flex>
            <FullWidth>
              <TextInput
                label={
                  <div title="Selected groups have read access to all tables under a namespace by default. Data edit access can be configured to these groups in table settings page.">
                    Access Groups
                  </div>
                }
                model="namespace.accessGroup"
                subText="Please verify that the groups you are adding are accurate."
                onKeyDown={(e) => {
                  return handleAccessKeyDown(e);
                }}
              />
            </FullWidth>
            <ButtonWrapper>
              <Button
                rounded
                size="$sm"
                variant="outline"
                onClick={handleAddAccessGroup}
              >
                <IconMaterial icon="add" />
              </Button>
            </ButtonWrapper>
          </Flex>
        </Card.Section>
        <Card.Section>
          <div>
            <For each="item" of={fields} index="index">
              <StyledRow key={item.group}>
                <Flex>
                  <FullWidth>{item.group}</FullWidth>
                  <ButtonWrapper>
                    <DangerButton
                      rounded
                      size="$sm"
                      onClick={() => {
                        return handleRemove(index);
                      }}
                    >
                      <IconMaterial
                        icon="delete"
                        color="$error1"
                        variant="outlined"
                      />
                    </DangerButton>
                  </ButtonWrapper>
                </Flex>
              </StyledRow>
            </For>
          </div>
        </Card.Section>
      </Card>
      {/* <AccessGroupRemoveModal
        isOpen={isOpen}
        setIsOpen={setIsOpen}
        removeAccessGroup={remove}
        removeIndex={removeIndex}
        removeAccessGroupName={removeAccessGroupName}
      /> */}
    </React.Fragment>
  );
};
