import { config } from '@abyss/web/tools/config';
import { useQuery } from '@src/abyss/web/hooks/useQuery';
import DeleteNamespace from './DeleteNamespace.graphql';

export const useDeleteNamespace = (options) => {
  return useQuery(DeleteNamespace, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    clearCache: [
      { key: 'GetNamespaces' },
      { key: 'GetNamespace', variables: ['namespaceKey'] },
      { key: 'GetTables', variables: ['namespaceKey'] },
    ],
  });
};
