import { config } from '@abyss/web/tools/config';
import { useQuery } from '@src/abyss/web/hooks/useQuery';
import GetNamespace from './GetNamespace.graphql';

export const useGetNamespace = (options) => {
  return useQuery(GetNamespace, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'namespace',
    initialState: {},
  });
};
