import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { Text } from '@abyss/web/ui/Text';
import { Card } from '@src/abyss/web/ui/Card';
import { DangerButton } from '@src/common/DangerButton';
import { DeleteNamespaceModal } from './DeleteNamespaceModal';

const DangerText = styled('div', {
  marginTop: '$lg',
  color: '$error1',
  fontWeight: '$bold',
  fontSize: '28px',
});

const DangerCard = styled(Card, {
  borderColor: '$error1',
});

const FlexGroup = styled('div', {
  display: 'flex',
  alignItems: 'center',
  flexWrap: 'wrap',
  justifyContent: 'space-between',
});

const FlexContainer = styled('div', {
  maxWidth: '50%',

  '@screen < $md': {
    marginTop: '$sm',
    maxWidth: '100%',
  },
});

export const NamespaceSettingsDanger = ({ namespaceKey }) => {
  const [isDeleteOpen, setIsDeleteOpen] = useState(false);

  const handleDeleteClick = () => {
    setIsDeleteOpen(true);
  };

  return (
    <React.Fragment>
      <DangerText>Danger Zone</DangerText>
      <DangerCard>
        <Card.Section>
          <FlexGroup>
            <FlexContainer>
              <Text fontWeight="$bold">Update namespace key</Text>
              <div>
                Updating your namespace key can have unintended side effects.
              </div>
            </FlexContainer>
            <FlexContainer>
              <DangerButton isDisabled>Update Namespace Key</DangerButton>
            </FlexContainer>
          </FlexGroup>
        </Card.Section>
        <Card.Section>
          <FlexGroup>
            <FlexContainer>
              <Text fontWeight="$bold">Delete namespace</Text>
              <div>
                Once deleted, it will be gone forever. Please be certain.
              </div>
            </FlexContainer>
            <FlexContainer>
              <DangerButton onClick={handleDeleteClick}>
                Delete this Namespace
              </DangerButton>
            </FlexContainer>
          </FlexGroup>
        </Card.Section>
      </DangerCard>
      <DeleteNamespaceModal
        namespaceKey={namespaceKey}
        isDeleteOpen={isDeleteOpen}
        setIsDeleteOpen={setIsDeleteOpen}
      />
    </React.Fragment>
  );
};

NamespaceSettingsDanger.propTypes = {
  namespaceKey: PropTypes.string.isRequired,
};
