import React from 'react';
import { styled } from '@abyss/web/tools/styled';
import { Button } from '@src/common/Button';
import { Modal } from '@abyss/web/ui/Modal';
import { Layout } from '@abyss/web/ui/Layout';

const StyledModal = styled(Modal, {
  '&.abyss-modal-content-container': {
    padding: '10px',
  },
});

export const WarningModal = ({ isOpen, onClose, onSubmit }) => {
  return (
    <StyledModal title="Warning" isOpen={isOpen} onClose={onClose}>
      <Modal.Section alignLayout="center">
        It is important to note that archiving a GitHub repository will break
        any existing deployment pipelines associated with it. This means that
        any automated processes or scripts that rely on the repository will no
        longer be able to access it, and may cause unexpected errors or issues.
      </Modal.Section>
      <Modal.Section>
        <Layout.Group alignLayout="right">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={onSubmit}>I Understand</Button>
        </Layout.Group>
      </Modal.Section>
    </StyledModal>
  );
};
