import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { TextInput } from '@abyss/web/ui/TextInput';
import { SelectInput } from '@abyss/web/ui/SelectInput';
import { Button } from '@src/common/Button';
import { Layout } from '@abyss/web/ui/Layout';
import { useCurrentUser } from '@src/hooks/user/useCurrentUser';
import { Card } from '@src/abyss/web/ui/Card';
import { Alert } from '@abyss/web/ui/Alert';
import { AccessGroups } from '../AccessGroups';

export const NamespaceSettingsForm = ({
  namespaceKey,
  alert,
  setAlert,
  isSuperAdmin,
}) => {
  const user = useCurrentUser();

  const userGroupOptions = useMemo(() => {
    return user.groups.map((groupName) => {
      return {
        label: groupName,
        value: groupName,
      };
    });
  }, [user.groups]);

  return (
    <React.Fragment>
      <Card>
        <Card.Section>
          <TextInput
            label="Namespace Display Name"
            model="namespace.displayName"
            validators={{ required: true }}
          />
          <Choose>
            <When condition={isSuperAdmin}>
              <TextInput
                label="Admin Group"
                model="namespace.adminGroup"
                validators={{ required: true }}
              />
            </When>
            <Otherwise>
              <SelectInput
                label="Admin Group"
                placeholder="Select group..."
                model="namespace.adminGroup"
                options={userGroupOptions}
                validators={{ required: true }}
                isSearchable
              />
            </Otherwise>
          </Choose>
        </Card.Section>
      </Card>
      <AccessGroups />
      <Alert
        isVisible={!!alert.message}
        title={alert.title}
        onClose={() => {
          setAlert({});
        }}
        css={{
          'abyss-alert-root': {
            marginTop: '$md',
          },
        }}
      >
        {alert.message}
      </Alert>
      <Card>
        <Card.Section>
          <Layout.Group alignLayout="right">
            <Button variant="outline" href={`/${namespaceKey}`}>
              Cancel
            </Button>
            <Button type="submit">Save Changes</Button>
          </Layout.Group>
        </Card.Section>
      </Card>
    </React.Fragment>
  );
};

NamespaceSettingsForm.propTypes = {
  namespaceKey: PropTypes.string.isRequired,
  alert: PropTypes.shape({
    message: PropTypes.string,
    title: PropTypes.string,
  }),
  setAlert: PropTypes.func,
  isSuperAdmin: PropTypes.bool,
};

NamespaceSettingsForm.defaultProps = {
  alert: {},
  setAlert: () => {
    return null;
  },
  isSuperAdmin: false,
};
