import React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { useAbyssProps } from '@abyss/web/hooks/useAbyssProps';
import { SubNavLink } from './SubNavLink';
import { SubNavDropdown } from './SubNavDropdown';
import { SubNavHeading } from './SubNavHeading';

const MenuItems = styled('ul', {
  display: 'flex',
  flexDirection: 'column',
  width: '100%',
});

const ListItem = styled('li', {
  width: '100%',
});

export const SubNavMenuItem = ({ menuItem, index, depth, ...props }) => {
  const abyssProps = useAbyssProps(props);

  if (menuItem.title) {
    if (depth > 0 && menuItem.subItems) {
      return (
        <ListItem {...abyssProps('sub-navigation-menu-item')}>
          <SubNavDropdown {...menuItem} depth={depth} />
        </ListItem>
      );
    }

    if (menuItem.href || menuItem.onClick) {
      return (
        <ListItem {...abyssProps('sub-navigation-menu-item')}>
          <SubNavLink {...menuItem} depth={depth} />
        </ListItem>
      );
    }

    return (
      <ListItem {...abyssProps('sub-navigation-menu-item')}>
        <SubNavHeading {...menuItem} />
        {menuItem.subItems && (
          <MenuItems {...props} {...abyssProps('sub-navigation-menu-items')}>
            {menuItem.subItems.map((subMenuItem, subIndex) => {
              return (
                <SubNavMenuItem
                  {...props}
                  // eslint-disable-next-line react/no-array-index-key
                  key={`${index}-${subIndex}`}
                  menuItem={subMenuItem}
                  index={subIndex}
                  depth={depth + 1}
                />
              );
            })}
          </MenuItems>
        )}
      </ListItem>
    );
  }

  return null;
};

SubNavMenuItem.propTypes = {
  menuItem: PropTypes.shape({
    href: PropTypes.string,
    isDisabled: PropTypes.bool,
    openNewWindow: PropTypes.bool,
    before: PropTypes.node,
    after: PropTypes.node,
    title: PropTypes.string,
    onClick: PropTypes.func,
    subItems: PropTypes.arrayOf(PropTypes.shape({})),
  }).isRequired,
  index: PropTypes.number.isRequired,
  depth: PropTypes.number,
};

SubNavMenuItem.defaultProps = {
  depth: 0,
};
