import React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@abyss/web/tools/styled';
import { useAbyssProps } from '@abyss/web/hooks/useAbyssProps';
import { useCssProps } from '@abyss/web/hooks/useCssProps';
import { SubNavHeader } from './SubNavHeader';
import { SubNavMenuItem } from './SubNavMenuItem';

const NavMenuContainer = styled('nav', {
  static: {
    paddingTop: '0',
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'flex-start',
    flexDirection: 'column',
    width: '100%',
    overflowY: 'auto',
    variants: {
      position: {
        right: {},
        left: {},
      },
      variant: {
        sideBarMenu: {
          border: 'none !important',
        },
        defaultVariants: {
          variant: 'default',
        },
      },
    },
  },
  dynamic: ({ cssProps }) => {
    const { width, height } = cssProps;
    return { width, height };
  },
});

const MenuItems = styled('ul', {
  display: 'flex',
  flexDirection: 'column',
  width: '100%',

  variants: {
    position: {
      right: {},
      left: {},
    },
    variant: {
      sideBarMenu: {
        marginTop: '5px !important',
      },

      defaultVariants: {
        variant: 'default',
      },
    },
  },
  compoundVariants: [
    {
      variant: 'sideBarMenu',
      position: 'right',
    },
    {
      variant: 'sideBarMenu',
      position: 'left',
    },
  ],
});

export const SubNavigationMenu = ({
  title,
  subText,
  navItems,
  position,
  variant,
  headingLevel,
  height,
  width,
  ...props
}) => {
  const abyssProps = useAbyssProps(props);
  const cssProps = useCssProps({ height, width });

  return (
    <NavMenuContainer
      position={position}
      variant={variant}
      cssProps={cssProps}
      {...abyssProps('sub-navigation-menu-root')}
      aria-label={title}
    >
      {title && (
        <SubNavHeader
          {...props}
          position={position}
          variant={variant}
          title={title}
          subText={subText}
          headingLevel={headingLevel}
        />
      )}
      <MenuItems
        {...props}
        position={position}
        variant={variant}
        {...abyssProps('sub-navigation-menu-items')}
      >
        {navItems.map((menuItem, index) => {
          return (
            <SubNavMenuItem
              {...props}
              // eslint-disable-next-line react/no-array-index-key
              key={index}
              menuItem={menuItem}
              index={index}
            />
          );
        })}
      </MenuItems>
    </NavMenuContainer>
  );
};

SubNavigationMenu.displayName = '@abyss/web/ui/SubNavigationMenu';

SubNavigationMenu.propTypes = {
  title: PropTypes.node,
  subText: PropTypes.string,
  height: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  width: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  navItems: PropTypes.arrayOf(PropTypes.shape({})),
  position: PropTypes.string,
  variant: PropTypes.oneOf(['default', 'sideBarMenu']),
  headingLevel: PropTypes.oneOf(['h1', 'h2', 'h3', 'h4', 'h5', 'h6']),
};

SubNavigationMenu.defaultProps = {
  navItems: [],
  position: 'right',
  height: '100%',
  width: '100%',
  variant: 'default',
  title: null,
  subText: null,
  headingLevel: 'h3',
};
